h1 {
  margin-bottom: 30px;
  margin-top: 10px;
}
.active {
  color: rgba(256, 256, 256, 1);
}

.linkColor {
  color: rgba(256, 256, 256, 0.5);
}

a {
  color: rgba(256, 256, 256, 0.5);
}

.headerStyle {
  background-color: rgb(33, 150, 243) !important;
  display: flex;
  flex-flow: wrap;
  justify-content: center;
  height: 130px !important;
}

.navStyle {
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: -10px;
}

.navStyle h5 {
  opacity: 0.8;
}

